export default class GameService {
  startGame () {
    const rtn = {}
    rtn.numbers = this.getNumbers()
    rtn.goal = this.getGoal(rtn.numbers)
    return rtn
  }

  getNumbers () {
    const smallnumbers = [1, 2, 3, 4, 5, 6, 7, 8, 9]
    const bignumbers = [10, 25, 50, 75, 100]
    const numbers = []
    let x
    for (let i = 0; i < 4; i++) {
      x = Math.floor(Math.random() * 8)
      numbers.push(smallnumbers[x])
    }
    for (let i = 0; i < 2; i++) {
      x = Math.floor(Math.random() * 4)
      numbers.push(bignumbers[x])
    }
    return numbers
  }

  getGoal (numbers) {
    let goal = 0
    while (!goal) {
      const arr = JSON.parse(JSON.stringify(numbers))
      const arrI = [0, 1, 2, 3, 4, 5]
      for (let i = 0; i < 3; i++) {
        const combination = this.setCombination(arr, arrI, i === 2)
        if (combination) {
          if (combination.n1 > combination.n2) {
            arrI.splice(combination.n1, 1)
            arrI.splice(combination.n2, 1)
          } else {
            arrI.splice(combination.n2, 1)
            arrI.splice(combination.n1, 1)
          }
          goal = combination.res
          arrI.push(i + 6)
          arr.push(goal)
        } else {
          goal = 0
        }
      }
      console.log('goal', goal)
      arrI.forEach(el => {
        if (el > 5 && el < arr.length - 1) {
          goal = 0
        }
      })
      console.log('goal2', goal)
    }
    return goal
  }

  setCombination (arr, arrI, last = false) {
    let combinations = []
    let start = 0
    console.log('arri', arrI)
    if (last) {
      console.log('arr', arr, arrI)
      console.log('arrl', arr.length - 2)
      console.log('arril', arrI[arrI.length - 2])
      if (arrI[arrI.length - 2] === arr.length - 2) {
        start = arrI.length - 2
      }
      console.log('start', start)
    }
    for (let n1 = start; n1 < arrI.length; n1++) {
      for (let n2 = start; n2 < arrI.length; n2++) {
        if (n2 !== n1) {
          if (!last || (n1 === arrI.length - 1) || (n2 === arrI.length - 1)) {
            const n1i = arrI[n1]
            const n2i = arrI[n2]
            const n1Val = arr[arrI[n1]]
            const n2Val = arr[arrI[n2]]
            if (n2 > n1) {
              combinations.push({ n1, n2, n1i, n2i, res: n1Val + n2Val, op: '+' })
              combinations.push({ n1, n2, n1i, n2i, res: n1Val * n2Val, op: 'x' })
            }
            if (arr[arrI[n1]] > arr[arrI[n2]]) {
              combinations.push({ n1, n2, n1i, n2i, res: n1Val - n2Val, op: '-' })
            }
            if (arr[arrI[n1]] >= arr[arrI[n2]] && arr[arrI[n2]] > 1) {
              const res = arr[arrI[n1]] / arr[arrI[n2]]
              if (!(res - Math.floor(res))) {
                combinations.push({ n1, n2, n1i, n2i, res: res, op: '/' })
              }
            }
          }
        }
      }
    }

    if (last) {
      combinations = combinations.filter(el => el.res > 100 && el.res < 1000)
    }
    const i = Math.floor(Math.random() * (combinations.length))
    console.log('combination', combinations[i])
    return combinations.length ? combinations[i] : false
  }

  getOperator (n1, n2, op) {
    const ops = ['-', '+', 'x', '/']
    if (n1 / n2 - Math.floor(n1 / n2)) {
      ops.splice(3, 1)
    }
    if (n2 >= n1) {
      ops.splice(0, 1)
    }
    op = op || ops[Math.floor(Math.random() * ops.length)]
    switch (op) {
      case '-':
        return n1 - n2
      case '+':
        return n1 + n2
      case 'x':
        return n1 * n2
      case '/':
        return n1 / n2
    }
  }
}
